import { PlayerType } from 'src/constants';

class CardModel {
  id: string;
  name: string;
  desc: string;
  attack: number;
  armor: number;
  mana: number;
  positive: boolean;
  target: PlayerType;
  source: PlayerType;
  extraInfo: string;
  playedTime: Date | null;


  constructor(info: any) {
    this.id = info.id;
    this.name = info.name;
    this.desc = info.desc;
    this.attack = info.attack;
    this.armor = info.armor;
    this.positive = info.positive;
    this.target = info.target;
    this.source = info.source;
    this.extraInfo = info.extraInfo;
    this.playedTime = null;
    this.mana = info.mana;
  }
}

export default CardModel;



